import * as React from 'react';
import TripDTO from '../../dto/TripDTO';
import Form, { FormComponentProps } from 'antd/lib/form/Form';
import { Icon, Button, Drawer, Row, Col, DatePicker, TimePicker } from 'antd';
import moment from 'moment';

export interface IDuplicateTripItem {
  date: moment.Moment;
  hour: moment.Moment;
}

export interface IDuplicateTripFormProps {
  visible: boolean;
  trip: TripDTO | undefined;
  onClose: () => void;
  onConfirm: (trip: TripDTO, newDates: IDuplicateTripItem[]) => void;
}

let id = 0;

class DuplicateTrip extends React.Component<
  IDuplicateTripFormProps & FormComponentProps
> {
  componentWillReceiveProps(
    nextProps: IDuplicateTripFormProps & FormComponentProps
  ) {
    const { trip } = nextProps;
    if (
      trip !== undefined &&
      JSON.stringify(this.props.trip) !== JSON.stringify(nextProps.trip)
    ) {
      this.props.form.setFieldsValue({
        keys: [],
        duplicateElements: []
      });
    }
  }

  private onDeleteDate = (k: any) => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const duplicateElements: IDuplicateTripItem[] =
      form.getFieldValue('duplicateElements') || [];
    if (keys.length === 1) {
      return;
    }

    form.setFieldsValue({
      keys: keys.filter((key: any) => key !== k),
      duplicateElements: duplicateElements.filter(
        (value: IDuplicateTripItem, index: number) => index !== k
      )
    });
  };

  private onAddDate = () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(id++);

    form.setFieldsValue({
      keys: nextKeys
    });
  };

  handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { duplicateElements } = values;
        const { trip } = this.props;
        this.props.onConfirm(
          trip as any,
          duplicateElements.filter((element: any) => !!element)
        );
      }
    });
  };

  private getInitialDateValue = (key: number) => {
    const { trip: tripToDuplicate } = this.props;
    const tripDate = moment(
      tripToDuplicate !== undefined ? tripToDuplicate.date : new Date()
    );
    const newTripDate = tripDate.add(key + 1, 'd');
    return newTripDate;
  };

  private getInitialHourValue = () => {
    const { trip: tripToDuplicate } = this.props;
    const dateForHour = new Date(
      2000,
      1,
      1,
      tripToDuplicate?.hour ? Number(tripToDuplicate.hour.substring(0, 2)) : 12,
      tripToDuplicate?.hour ? Number(tripToDuplicate.hour.substring(3, 5)) : 0
    );
    const tripHour = moment(dateForHour);
    return tripHour;
  };

  public render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    getFieldDecorator('keys', { initialValue: [] });
    const keys = getFieldValue('keys');
    const myFormItems = keys.map((key: any, index: number) => (
      <Row gutter={16} key={index}>
        <Col span={11}>
          <Form.Item>
            {getFieldDecorator(`duplicateElements[${key}].date`, {
              initialValue: this.getInitialDateValue(index),
              rules: [
                {
                  required: true,
                  message: 'Selezionare data del viaggio'
                }
              ]
            })(<DatePicker style={{ width: '100%' }} format="DD-MM-YYYY" />)}
          </Form.Item>
        </Col>
        <Col span={11}>
          <Form.Item>
            {getFieldDecorator(`duplicateElements[${key}].hour`, {
              initialValue: this.getInitialHourValue(),
              rules: [
                {
                  type: 'object',
                  required: true,
                  message: `Specificare l'ora del servizio`
                }
              ]
            })(
              <TimePicker
                style={{ width: '100%' }}
                placeholder="Ora servizio"
                format="HH:mm"
              />
            )}
          </Form.Item>
        </Col>
        <Col span={2}>
          {key === 0 ? (
            undefined
          ) : (
            <Icon
              style={{ marginTop: '10px' }}
              className="dynamic-delete-button"
              type="minus-circle-o"
              onClick={() => this.onDeleteDate(key)}
            />
          )}
        </Col>
      </Row>
    ));

    return (
      <Drawer
        title={`Duplica servizio`}
        width={480}
        onClose={this.props.onClose}
        visible={this.props.visible}
        bodyStyle={{ paddingBottom: 80 }}
      >
        <Form onSubmit={this.handleSubmit}>
          {myFormItems}
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={this.onAddDate}
                  style={{ width: '100%' }}
                >
                  <Icon type="plus" /> Aggiungi data
                </Button>
              </Form.Item>
            </Col>
          </Row>
          <div
            style={{
              position: 'absolute',
              right: 0,
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e9e9e9',
              padding: '10px 16px',
              background: '#fff',
              textAlign: 'right'
            }}
          >
            <Button onClick={this.props.onClose} style={{ marginRight: 8 }}>
              Annulla
            </Button>
            <Button htmlType="submit" type="primary">
              Conferma
            </Button>
          </div>
        </Form>
      </Drawer>
    );
  }
}

const DuplicateTripContainer = Form.create({ name: 'duplicate_trip_form ' })(
  DuplicateTrip
);
class DuplicateTripForm extends React.Component<IDuplicateTripFormProps> {
  public render() {
    return <DuplicateTripContainer {...this.props} />;
  }
}

export default DuplicateTripForm;
