import * as React from 'react';
import { Drawer, Form, Col, Row, Input, InputNumber, DatePicker, TimePicker, Button, Radio, Select, Switch } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import TripDTO, { ServiceTargetLocationType, FlgArType, ServiceTargetRoleType } from '../../dto/TripDTO';
import moment from 'moment';
import VehicleDTO, { VehicleType } from '../../dto/VehicleDTO';

const { Option } = Select;

export interface ITripFormProps {
  visible: boolean;
  vehicles: VehicleDTO[];
  trip: TripDTO | undefined;
  onClose: () => void;
  onSave: (trip: TripDTO) => void;
}

class Trip extends React.Component<ITripFormProps & FormComponentProps> {
  private setFieldsValue = (trip: TripDTO) => {
    const { form } = this.props;
    const dateForHour = new Date(2000, 1, 1, trip.hour ? Number(trip.hour.substring(0, 2)) : 12, trip.hour ? Number(trip.hour.substring(3, 5)) : 0);
    const formValues = {
      ...trip,
      tripDate: trip.date ? moment(trip.date) : undefined,
      tripHour: moment(dateForHour)
    };
    form.setFieldsValue(formValues);
  };

  componentWillReceiveProps(nextProps: ITripFormProps & FormComponentProps) {
    const { trip } = nextProps;
    if (trip !== undefined && JSON.stringify(this.props.trip) !== JSON.stringify(nextProps.trip)) {
      this.setFieldsValue(trip as TripDTO);
    }
  }

  private handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { vehicleId } = values;
        const tripToSave: TripDTO = {
          ...values,
          vehicleId: vehicleId === undefined ? null : vehicleId,
          date: values.tripDate ? values.tripDate.format('YYYY-MM-DD') : undefined,
          hour: values.tripHour.format('HH:mm')
        };
        this.props.onSave(tripToSave);
      }
    });
  };

  private buildVehicleOptions = () => {
    const { vehicles } = this.props;
    const actualVehicles = vehicles.map((vehicle: VehicleDTO) => {
      const vehicleId: number = vehicle.id;
      return (
        <Option key={vehicleId} value={vehicle.id}>
          {vehicle.sign} - {VehicleType[vehicle.type]}
        </Option>
      );
    });
    const nullOptionVehicle = (
      <Option key={-1} value={undefined}>
        <div>&nbsp;</div>
      </Option>
    );
    return [nullOptionVehicle, ...actualVehicles];
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    const { trip, onClose, visible } = this.props;
    return (
      <Drawer title={`${trip?.id === 0 ? 'Crea' : 'Modifica'} servizio`} width={480} onClose={onClose} visible={visible} bodyStyle={{ paddingBottom: 80 }}>
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row gutter={16}>
            <Col span={8}>
              <Form.Item label="Data del servizio">
                {getFieldDecorator('tripDate', {
                  rules: [{ required: true, message: 'Specificare data di partenza' }]
                })(<DatePicker style={{ width: '100%' }} format="DD-MM-YYYY" />)}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Ora">
                {getFieldDecorator('tripHour', {
                  rules: [
                    {
                      type: 'object',
                      required: true,
                      message: `Specificare l'ora del servizio`
                    }
                  ]
                })(<TimePicker style={{ width: '100%' }} placeholder="Ora servizio" format="HH:mm" />)}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Luogo">
                {getFieldDecorator('flgDestination', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il luogo'
                    }
                  ]
                })(
                  <Select placeholder="Luogo">
                    <Option value={0}>{ServiceTargetLocationType[0]}</Option>
                    <Option value={1}>{ServiceTargetLocationType[1]}</Option>
                    <Option value={2}>{ServiceTargetLocationType[2]}</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item label="Partenza">
                {getFieldDecorator('siteDeparture', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il luogo di partenza'
                    }
                  ]
                })(<Input.TextArea rows={4} placeholder="Luogo di partenza" />)}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item label="Arrivo">
                {getFieldDecorator('siteArrival', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il luogo di arrivo'
                    }
                  ]
                })(<Input.TextArea rows={4} placeholder="Luogo di arrivo" />)}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <Form.Item label="Veicolo">{getFieldDecorator('vehicleId')(<Select>{this.buildVehicleOptions()}</Select>)}</Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item>
                {getFieldDecorator('flgAr')(
                  <Radio.Group style={{ marginTop: '10px' }}>
                    <Radio value={0}>{FlgArType[0]}</Radio>
                    <Radio value={1}>{FlgArType[1]}</Radio>
                  </Radio.Group>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Servizio per">
                {getFieldDecorator('type', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il tipo'
                    }
                  ]
                })(
                  <Select placeholder="Servizio per">
                    <Option value={0}>{ServiceTargetRoleType[0]}</Option>
                    <Option value={1}>{ServiceTargetRoleType[1]}</Option>
                    <Option value={2}>{ServiceTargetRoleType[2]}</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <Form.Item label="Tipologia servizio">
                {getFieldDecorator('serviceType', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il tipo di servizio'
                    }
                  ]
                })(<Input.TextArea rows={2} placeholder="Tipologia servizio" />)}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Dati paziente">{getFieldDecorator('patientData')(<Input.TextArea rows={2} placeholder="Dati paziente" />)}</Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Medico/sanitario">{getFieldDecorator('flgNurse', { valuePropName: 'checked' })(<Switch />)}</Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <Form.Item label="Note">{getFieldDecorator('notes')(<Input.TextArea rows={3} placeholder="Eventuali note aggiuntive" />)}</Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Numero accompagnatori">{getFieldDecorator('extraUsers', { initialValue: 1 })(<InputNumber min={1} max={10} />)}</Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Da notificare">{getFieldDecorator('flgToNotify', { valuePropName: 'checked' })(<Switch />)}</Form.Item>
            </Col>
          </Row>
          <Row gutter={16} style={{ visibility: 'collapse' }}>
            <Col span={6}>
              <Form.Item label="Id">{getFieldDecorator('id')(<Input placeholder="Specificare id servizio" />)}</Form.Item>
              <Col span={6}>
                <Form.Item label="CommitteeId">{getFieldDecorator('committeeId')(<Input placeholder="CommitteeId" />)}</Form.Item>
              </Col>
            </Col>
          </Row>
          <div
            style={{
              position: 'absolute',
              right: 0,
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e9e9e9',
              padding: '10px 16px',
              background: '#fff',
              textAlign: 'right'
            }}
          >
            <Button onClick={this.props.onClose} style={{ marginRight: 8 }}>
              Annulla
            </Button>
            <Button htmlType="submit" type="primary">
              Salva
            </Button>
          </div>
        </Form>
      </Drawer>
    );
  }
}

const TripContainer = Form.create({ name: 'trip_form ' })(Trip);
class TripForm extends React.Component<ITripFormProps> {
  public render() {
    return <TripContainer {...this.props} />;
  }
}

export default TripForm;
