import * as React from 'react';
import TripUsersDTO, { ServiceRoleType } from '../../dto/TripUsersDTO';
import { Button, Radio, Row, Select } from 'antd';
import { RadioChangeEvent } from 'antd/lib/radio';
import UserDTO from '../../dto/UserDTO';

const { Option } = Select;

interface RoleChooseProps {
  users: UserDTO[];
  tripUsers: TripUsersDTO[];
  isUserAdmin: boolean;
  loggedUser: UserDTO;
  onConfirmRole: (role: number, userId: number) => void;
}

interface RoleChooseState {
  roleSelected: number;
  userSelected?: UserDTO;
}

class RoleChoose extends React.Component<RoleChooseProps, RoleChooseState> {
  constructor(props: RoleChooseProps) {
    super(props);
    const users = this.getAvailableUsers();
    const initialUser: UserDTO | undefined = users.length > 0 ? users[0] : undefined;
    this.state = {
      userSelected: initialUser,
      roleSelected: 1
    };
  }

  private _onChangeRole = (e: RadioChangeEvent) => {
    this.setState({ roleSelected: e.target.value });
  };

  private _onChangeUser = (value: number) => {
    const { users } = this.props;
    const userSelected: UserDTO | undefined = users.find((user: UserDTO) => user.id === value);
    if (userSelected !== undefined) {
      this.setState({ userSelected, roleSelected: 1 });
    }
  };

  private getAvailableUsers = (): UserDTO[] => {
    const { users, tripUsers, isUserAdmin, loggedUser } = this.props;
    return users
      .filter((user: UserDTO) => !tripUsers.find((tripUser: TripUsersDTO) => tripUser.userId === user.id))
      .filter((user: UserDTO) => isUserAdmin || user.id === loggedUser.id);
  };

  private _onConfirmRole = () => {
    const { roleSelected, userSelected } = this.state;
    if (userSelected !== undefined) {
      this.props.onConfirmRole(roleSelected, userSelected.id);
    }
  };

  public render() {
    const { userSelected, roleSelected } = this.state;
    const avilabledUsers = this.getAvailableUsers();
    return (
      <div>
        <Row>
          <Radio.Group style={{ marginTop: '5px', width: '100%' }} value={roleSelected} buttonStyle="solid" onChange={this._onChangeRole}>
            {userSelected && userSelected.withDrivingLicense === 1 ? <Radio.Button value={0}>{ServiceRoleType[0]}</Radio.Button> : undefined}
            <Radio.Button value={1}>{ServiceRoleType[1]}</Radio.Button>
            {userSelected && userSelected.committeeUser.occupation === 2 ? <Radio.Button value={2}>{ServiceRoleType[2]}</Radio.Button> : undefined}
          </Radio.Group>
        </Row>
        <Row>
          <Select style={{ width: '100%' }} defaultValue={userSelected !== undefined ? userSelected.id : undefined} onChange={this._onChangeUser}>
            {avilabledUsers.map((user: UserDTO) => (
              <Option value={user.id} key={user.id}>
                {user.name} {user.surname}
              </Option>
            ))}
          </Select>
        </Row>
        <Row>
          <Button style={{ marginTop: '5px', width: '100%' }} type="primary" ghost onClick={this._onConfirmRole}>
            Conferma
          </Button>
        </Row>
      </div>
    );
  }
}

export default RoleChoose;
