import * as React from 'react';
import { Layout } from 'antd';
import { RouterProps } from 'react-router';
import RedCrossMenu from './base/RedCrossMenu';
const { Sider } = Layout;

export interface RedCrossSiderProps extends RouterProps {
  isUserAdmin: boolean;
}

interface RedCrossSiderState {
  collapsed: boolean;
}

class RedCrossSider extends React.Component<RedCrossSiderProps, RedCrossSiderState> {
  constructor(props: RedCrossSiderProps) {
    super(props);
    this.state = {
      collapsed: true
    };
  }

  _onCollapse = (collapsed: any) => {
    this.setState({ collapsed });
  };

  public render() {
    const { isUserAdmin, history } = this.props;
    return (
      <Sider collapsible collapsed={this.state.collapsed} onCollapse={this._onCollapse}>
        <div style={{ height: '60px', width: '100%' }} />
        <RedCrossMenu isUserAdmin={isUserAdmin} history={history} menuMode={'inline'} />
      </Sider>
    );
  }
}

export default RedCrossSider;
