import { combineReducers } from 'redux';
import { TReducerActions, ReduxActionTypes } from '../actions/action';
import { Connector } from '../utils/Connector';
import UserDTO from '../dto/UserDTO';
import CommitteeDTO from '../dto/CommitteeDTO';
import { resetUserInfo } from '../utils/Utils';

export interface AppDataState extends WithUser, WithCommittee, WIthConnector {
  user: UserDTO | undefined;
  accessToken: string;
  committee: CommitteeDTO | undefined;
  users: UserDTO[];
  showLoading: boolean;
  connector: Connector;
}

const stateDefault: AppDataState = {
  user: undefined,
  accessToken: '',
  committee: undefined,
  users: [],
  connector: new Connector(),
  showLoading: false
};

export interface WithUser {
  user: UserDTO | undefined;
}

export interface WithUsers {
  users: UserDTO[];
}

export interface WithCommittee {
  committee: CommitteeDTO | undefined;
}

export interface WIthConnector {
  connector: Connector;
}

const appData = (state: AppDataState = stateDefault, action: TReducerActions): AppDataState => {
  switch (action.type) {
    case ReduxActionTypes.SET_ACCESS_TOKEN:
      const { accessToken } = action.data;
      const connector = new Connector();
      return {
        ...state,
        accessToken,
        connector
      };
    case ReduxActionTypes.SET_USER_DATA:
      const { user, committeeId } = action.data;
      let normalizedUser = (user && { ...user }) || undefined;
      if (user) {
        if ((user as any).committee_users) {
          normalizedUser = {
            ...user,
            committeeUser: (user as any).committee_users.find((committee_user: any) => committee_user.committeeId === committeeId)
          };
        }
      }
      const newState = {
        ...state,
        user: normalizedUser
      };
      return newState;
    case ReduxActionTypes.SET_COMMITTEE_DATA:
      const { committee } = action.data;
      return {
        ...state,
        committee
      };
    case ReduxActionTypes.SET_SHOW_LOADING:
      return { ...state, showLoading: action.data };
    case ReduxActionTypes.RESET_AUTH_DATA:
      resetUserInfo();
      return {
        ...state,
        user: undefined,
        committee: undefined,
        connector: new Connector(),
        accessToken: '',
        users: []
      };
    case ReduxActionTypes.SET_USERS_DATA:
      const { users } = action.data;
      const normalizedUsers: any = (users || []).map((userToNormalize: UserDTO) => {
        return {
          ...userToNormalize,
          committeeUser: (userToNormalize as any).committee_users[0]
        };
      });
      return {
        ...state,
        users: normalizedUsers
      };
    default:
      return state;
  }
};

const AppReducers = combineReducers({
  appData
});

export default AppReducers;
