import * as React from 'react';
import { connect } from 'react-redux';
import { WIthConnector, WithCommittee, WithUser, WithUsers } from '../reducers/reducers';
import { Spin, Button, Popconfirm, Icon, Tooltip, Drawer } from 'antd';
import { message, Table } from 'antd';
import 'react-tiny-fab/dist/styles.css';
import PlusFabButton from './base/PlusFabButton';
import UserDTO, { UserRoleType, UserOccupationType } from '../dto/UserDTO';
import UserForm from './forms/UserForm';
import { setUsersData, ISetUsersDataProps } from '../actions/action';
import TripUsersDTO from '../dto/TripUsersDTO';

interface IUsersState {
  loading: boolean;
  showForm: boolean;
  selectedUser?: UserDTO;
}

class Users extends React.Component<WithUser & WithUsers & WIthConnector & WithCommittee & ISetUsersDataProps, IUsersState> {
  columns = [
    {
      title: 'Username',
      dataIndex: 'username',
      key: 'username',
      render: (value: any, record: any) => <b>{value}</b>
    },
    {
      title: 'Nome',
      dataIndex: 'name',
      key: 'name'
    },
    {
      title: 'Cognome',
      dataIndex: 'surname',
      key: 'surname'
    },
    {
      title: 'Data di nascita',
      dataIndex: 'birth_date',
      key: 'birth_date'
    },
    {
      title: 'Ruolo',
      dataIndex: 'committeeUser.role',
      key: 'role',
      render: (value: any, record: any) => <span>{UserRoleType[value]}</span>
    },
    {
      title: 'Occupazione',
      dataIndex: 'committeeUser.occupation',
      key: 'occupation',
      render: (value: any, record: any) => <span>{UserOccupationType[value]}</span>
    },
    {
      title: 'Con patente',
      dataIndex: 'withDrivingLicense',
      key: 'withDrivingLicense',
      render: (value: any, record: any) => <p>{value === 0 ? undefined : <Icon type="check" />}</p>
    },
    {
      title: 'Disabilitato',
      dataIndex: 'committeeUser.disabled',
      key: 'disabled',
      render: (value: any, record: any) => <p>{value === 0 ? undefined : <Icon type="check" />}</p>
    },
    {
      title: '',
      key: 'action',
      render: (text: any, record: UserDTO) => (
        <span>
          <Tooltip title="Modifica">
            <Button size="small" icon="edit" onClick={() => this._onEditRow(record)} />
          </Tooltip>
          <Popconfirm
            placement="topLeft"
            title={`Confermare l'eliminazione dell'utente?`}
            onConfirm={() => this._onDeleteUser(record)}
            okText="Si"
            cancelText="No"
          >
            <Button size="small" style={{ marginLeft: '1px' }} icon="delete" type="danger" />
          </Popconfirm>
        </span>
      )
    }
  ];

  emptyUser: UserDTO = {
    id: 0,
    username: '',
    password: '',
    flgPasswordChanged: 0,
    name: '',
    surname: '',
    birth_date: undefined,
    email: undefined,
    gender: 0,
    withDrivingLicense: 0,
    superuser: 0,
    committeeUser: {
      id: 0,
      committeeId: this.props.committee?.id || 0,
      userId: 0,
      role: 2,
      occupation: 0,
      disabled: 0
    }
  };

  state: IUsersState = {
    loading: false,
    showForm: false
  };

  private loadData = () => {
    const { committee } = this.props;
    if (committee !== undefined) {
      this.props.connector
        .getUsers(committee.id)
        .then((res) => {
          this.setState({ loading: false }, () => {
            this.props.setUsersData(res.data);
          });
        })
        .catch((err) => {
          message.error(`Si è verificato un errore nella lettura degli utenti`);
          this.setState({ loading: false });
          console.error(err);
        });
    }
  };

  private _onClose = () => {
    this.setState({ showForm: false, selectedUser: undefined });
  };

  private _onEditRow = (selectedUser: UserDTO) => {
    this.setState({ selectedUser, showForm: true });
  };

  private _onDeleteUser = (user: UserDTO) => {
    this.props.connector
      .getUserTrips(user.id)
      .then((res) => {
        const tripUsers: TripUsersDTO[] = res.data || [];
        if (tripUsers.length > 0) {
          message.error(`Utente già assegnato a servizi. Impossibile eliminare`);
          return;
        }
        this.props.connector
          .deleteCommitteesUsers(user.id)
          .then((res) => {
            this.props.connector
              .deleteUser(user.id)
              .then((res) => {
                this.loadData();
              })
              .catch((err) => {
                console.error(`_onDeleteUser - deleteUser: ${err}`);
                message.error(`Si è verificato un errore durante l'eliminazione`);
              });
          })
          .catch((err) => {
            console.error(`_onDeleteUser - deleteCommitteesUsers: ${err}`);
            message.error(`Si è verificato un errore durante l'eliminazione`);
          });
      })
      .catch((err) => {
        console.error(`_onDeleteUser - getUserTrips: ${err}`);
        message.error(`Si è verificato un errore durante l'eliminazione`);
      });
  };

  private _onSave = (user: UserDTO) => {
    const { committeeUser } = user;
    if (!committeeUser) {
      console.warn(`Expected NOT NULL 'committee' - This should never happen!`);
      message.error('Si è verificato un errore nel salvataggio');
      return;
    }
    this.props.connector
      .publishUserCommittee(user, committeeUser)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ showForm: false, selectedUser: undefined }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onCreate = () => {
    this.setState({ selectedUser: this.emptyUser, showForm: true });
  };

  private getDataSource = (): UserDTO[] => {
    const { user, users } = this.props;
    if (user !== undefined) {
      // Un utente NON superuser può gestire solo utenti Admin e User
      if (user.superuser === 0) {
        return users.filter((user: UserDTO) => user.superuser === 0);
      }
      return users;
    } else {
      return [];
    }
  };

  public render() {
    const { loading, selectedUser, showForm } = this.state;
    return (
      <div>
        {loading ? (
          <div style={{ textAlign: 'center' }}>
            <Spin />
          </div>
        ) : (
          <div>
            <Table columns={this.columns} pagination={{ pageSize: 10 }} dataSource={this.getDataSource()} />
            <Drawer
              title={`${(selectedUser as any)?.id === 0 ? 'Crea' : 'Modifica'} utente`}
              width={480}
              onClose={this._onClose}
              visible={showForm}
              bodyStyle={{ paddingBottom: 80 }}
            >
              {showForm ? <UserForm loggedUserId={this.props.user?.id || 0} user={selectedUser} onCancel={this._onClose} onSave={this._onSave} /> : undefined}
            </Drawer>
            {showForm ? undefined : <PlusFabButton onClick={this._onCreate} />}
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee,
    users: state.appData.users
  };
};
const UsersContainer = connect(mapStateToProps, { setUsersData })(Users);
export default UsersContainer;
