import * as React from 'react';
import { Drawer, Form, Button, Col, Row, Input, Select, Switch } from 'antd';
import VehicleDTO, { VehicleType } from '../../dto/VehicleDTO';
import { FormComponentProps } from 'antd/lib/form/Form';

const { Option } = Select;

export interface IVehicleFormProps {
  visible: boolean;
  vehicle: VehicleDTO | undefined;
  onClose: () => void;
  onSave: (vehicle: VehicleDTO) => void;
}

class Vehicle extends React.Component<IVehicleFormProps & FormComponentProps> {
  componentWillReceiveProps(nextProps: IVehicleFormProps & FormComponentProps) {
    const { vehicle } = nextProps;
    if (
      vehicle !== undefined &&
      JSON.stringify(this.props.vehicle) !== JSON.stringify(nextProps.vehicle)
    ) {
      const { form } = nextProps;
      form.setFieldsValue({
        type: vehicle.type,
        plate: vehicle.plate,
        sign: vehicle.sign,
        description: vehicle.description,
        disabled: vehicle.disabled
      });
    }
  }

  private handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { vehicle } = this.props;
        const vehicleToSave: VehicleDTO = {
          ...values,
          id: (vehicle as any).id,
          committeeId: (vehicle as any).committeeId,
          committee: (vehicle as any).committee
        };
        this.props.onSave(vehicleToSave);
      }
    });
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Drawer
        title={`${this.props.vehicle?.id === 0 ? 'Crea' : 'Modifica'} veicolo`}
        width={480}
        onClose={this.props.onClose}
        visible={this.props.visible}
        bodyStyle={{ paddingBottom: 80 }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item label="Tipo">
                {getFieldDecorator('type', {
                  rules: [
                    {
                      required: true,
                      message: 'Specificare il tipo'
                    }
                  ],
                  initialValue: '0'
                })(
                  <Select placeholder="Tipologia">
                    <Option value={0}>{VehicleType[0]}</Option>
                    <Option value={1}>{VehicleType[1]}</Option>
                    <Option value={2}>{VehicleType[2]}</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item label="Targa">
                {getFieldDecorator('plate', {
                  rules: [{ required: true, message: 'Specificare la targa' }]
                })(<Input placeholder="Specificare la targa" />)}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item label="Sigla">
                {getFieldDecorator('sign', {
                  rules: [{ required: false, message: 'Specificare la sigla' }]
                })(<Input placeholder="Specificare una sigla" />)}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item label="Descrizione">
                {getFieldDecorator('description', {
                  rules: [
                    {
                      required: false
                    }
                  ]
                })(
                  <Input.TextArea
                    rows={4}
                    placeholder="Specificare una descrizione"
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={12}>
              <Form.Item label="Disabilita">
                {getFieldDecorator('disabled', { valuePropName: 'checked' })(
                  <Switch />
                )}
              </Form.Item>
            </Col>
          </Row>
          <div
            style={{
              position: 'absolute',
              right: 0,
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e9e9e9',
              padding: '10px 16px',
              background: '#fff',
              textAlign: 'right'
            }}
          >
            <Button onClick={this.props.onClose} style={{ marginRight: 8 }}>
              Annulla
            </Button>
            <Button htmlType="submit" type="primary">
              Salva
            </Button>
          </div>
        </Form>
      </Drawer>
    );
  }
}

const VehicleContainer = Form.create({ name: 'vehicle_form ' })(Vehicle);
class VehicleForm extends React.Component<IVehicleFormProps> {
  public render() {
    return <VehicleContainer {...this.props} />;
  }
}

export default VehicleForm;
