import * as React from 'react';
import { Fab } from 'react-tiny-fab';

export interface PlusFabButtonProps {
  onClick: () => void;
}

class PlusFabButton extends React.Component<PlusFabButtonProps, any> {
  public render() {
    return (
      <Fab
        mainButtonStyles={{ backgroundColor: '#1890ff' }}
        alwaysShowTitle={false}
        event={'click'}
        icon={<span>+</span>}
        position={{ bottom: 25, right: 25 }}
        onClick={this.props.onClick}
      />
    );
  }
}

export default PlusFabButton;
