import React from 'react';
import { Row, Button, Col, Card, Radio, message, Timeline, Tag, Popconfirm, Avatar, Empty, Tooltip, Badge, Popover } from 'antd';
import { add, format } from 'date-fns';
import TripDTO, { ServiceTargetRoleType, ServiceTargetLocationType, FlgArType } from '../dto/TripDTO';
import { connect } from 'react-redux';
import { WithUser, WIthConnector, WithCommittee, WithUsers } from '../reducers/reducers';
import VehicleDTO, { VehicleType } from '../dto/VehicleDTO';
import TripUsersDTO, { ServiceRoleType } from '../dto/TripUsersDTO';
import UserDTO from '../dto/UserDTO';
import PlusFabButton from './base/PlusFabButton';
import TripForm from './forms/TripForm';
import RoleChoose from './base/RoleChoose';
import DuplicateTripForm, { IDuplicateTripItem } from './forms/DuplicateTripForm';

interface IServicesState {
  loading: boolean;
  selectedDate: Date;
  firstDayOfView: Date;
  viewMode: ViewModeType;
  trips: TripDTO[];
  vehicles: VehicleDTO[];
  showForm: boolean;
  showDuplicateTripForm: boolean;
  selectedTrip: TripDTO | undefined;
  rolesVisible: Array<boolean>;
}

enum ViewModeType {
  week,
  month
}

const WeekDays: Array<string> = ['Domenica', 'Lunedì', 'Martedì', 'Mercoledì', 'Giovedì', 'Venerdì', 'Sabato'];

const Months: Array<string> = [
  'Gennaio',
  'Febbraio',
  'Marzo',
  'Aprile',
  'Maggio',
  'Giugno',
  'Luglio',
  'Agosto',
  'Settembre',
  'Ottobre',
  'Novembre',
  'Dicembre'
];

const today = new Date();

class Services extends React.Component<WithUser & WIthConnector & WithCommittee & WithUsers, IServicesState> {
  state = {
    loading: false,
    selectedDate: today,
    firstDayOfView: today,
    viewMode: ViewModeType.week,
    trips: [],
    vehicles: [],
    showForm: false,
    showDuplicateTripForm: false,
    selectedTrip: undefined,
    rolesVisible: []
  };

  componentDidMount() {
    this.loadData();
  }

  private getEmptyTrip = (): TripDTO => {
    const { vehicles } = this.state;
    const emptyTrip: TripDTO = {
      id: 0,
      date: format(this.state.selectedDate, 'yyyy-MM-dd'),
      hour: '12:00:00',
      flgDestination: 0,
      flgAr: 0,
      siteDeparture: '',
      siteArrival: '',
      serviceType: '',
      patientData: undefined,
      type: 2,
      extraUsers: 1,
      flgNurse: 0,
      flgToNotify: 0,
      notes: undefined,
      committeeId: this.props.committee?.id || 0,
      vehicleId: vehicles.length !== 0 ? (vehicles[0] as any).id : 0,
      committee: undefined as any,
      vehicle: undefined as any,
      trip_users: []
    };
    return emptyTrip;
  };

  private loadData = () => {
    const { committee } = this.props;
    if (committee !== undefined) {
      const committeeId: number = committee.id;
      this.props.connector
        .getTrips(committeeId)
        .then((res) => {
          this.setState({
            trips: res.data
          });
          this.props.connector
            .getVehicles(committeeId)
            .then((vRes) => {
              this.setState({
                vehicles: vRes.data,
                loading: false
              });
            })
            .catch((vErr) => {
              this.setState({ vehicles: [], loading: false });
              console.error(vErr);
            });
        })
        .catch((err) => {
          this.setState({ trips: [], loading: false });
          console.error(err);
        });
    }
  };

  private _onChangeView = (e: any) => {
    const viewMode: any = e.target.value;
    this.setState({ viewMode });
  };

  private _onClickDayOfWeek = (date: Date) => {
    console.log(date);
    this.setState({ selectedDate: date });
  };

  private renderCardHeaderDate(date: Date) {
    const dayOfMonth: number = date.getDate();
    const month: string = Months[date.getMonth()];
    return (
      <Row>
        <Col style={{ textAlign: 'left' }} span={24}>
          {dayOfMonth} {month.substring(0, 3)}
        </Col>
      </Row>
    );
  }

  private renderCardBodyWeekDay(date: Date) {
    const weekDay: string = WeekDays[date.getDay()];
    const todayTrips: TripDTO[] = this.getTrips(date);
    return (
      <Row>
        <Col style={{ textAlign: 'left' }} span={24}>
          <b>{weekDay}</b>
          {todayTrips.length !== 0 ? <Badge count={todayTrips.length} style={{ backgroundColor: '#87D068', marginLeft: '3px' }} /> : undefined}
        </Col>
      </Row>
    );
  }

  private renderDayOfWeek = (date: Date) => {
    const { selectedDate } = this.state;
    const isSelected =
      date.getDate() === selectedDate.getDate() && date.getMonth() === selectedDate.getMonth() && date.getFullYear() === selectedDate.getFullYear();
    const isOld = date.getTime() < today.getTime();
    const isToday = date.getTime() === today.getTime();
    return (
      <Col key={date.getTime()} span={3}>
        <Card
          onClick={() => this._onClickDayOfWeek(date)}
          size="small"
          title={this.renderCardHeaderDate(date)}
          className="current-day"
          style={{
            cursor: 'pointer',
            borderRadius: '10px',
            height: '90px',
            border: `2px solid ${isSelected ? '#1890ff' : '#e8e8e8'}`,
            backgroundColor: `${isToday ? '#e6f7ff' : isOld ? '#f2f4f5' : 'white'}`
          }}
        >
          {this.renderCardBodyWeekDay(date)}
        </Card>
      </Col>
    );
  };

  private renderDays = () => {
    const { viewMode, firstDayOfView } = this.state;
    const numRows: number = viewMode === ViewModeType.week ? 1 : 4;
    const rows = [];

    for (let rowIndex = 0; rowIndex < numRows; rowIndex++) {
      const days = this.renderDaysOfWeek(firstDayOfView, rowIndex);
      const row = (
        <Row justify="center" key={rowIndex} type="flex">
          <Col span={1} style={{ textAlign: 'center' }}>
            {rowIndex === 0 ? <Button icon="left" style={{ margin: 5 }} onClick={this._onMoveViewLeft} /> : undefined}
          </Col>
          {days}
          <Col span={1} style={{ textAlign: 'center' }}>
            {rowIndex === 0 ? <Button icon="right" style={{ margin: 5 }} onClick={this._onMoveViewRight} /> : undefined}
          </Col>
        </Row>
      );
      rows.push(row);
    }

    return rows;
  };

  private renderDaysOfWeek = (startDate: Date, rowIndex: number) => {
    const elements: any = [];
    for (let index = 0; index < 7; index++) {
      var result = add(startDate, {
        days: index + rowIndex * 7
      });
      elements.push(this.renderDayOfWeek(result));
    }
    return elements;
  };

  private _onDeleteTrip = (tripId: number) => {
    this.props.connector
      .deleteTripUsersByTrip(tripId)
      .then((res) => {
        this.props.connector
          .deleteTrip(tripId)
          .then((tRes) => {
            this.loadData();
          })
          .catch((tErr) => {
            console.log(`_onDeleteTrip error: ${tErr}`);
            message.error(`Si è verificato un errore durante l'eliminazione`);
            this.loadData();
          });
      })
      .catch((err) => {
        console.log(`_onDeleteTrip error: ${err}`);
        message.error(`Si è verificato un errore durante l'eliminazione`);
      });
  };

  private _onEditTrip = (trip: TripDTO) => {
    this.setState({ selectedTrip: trip, showForm: true });
  };

  private _onDuplicateTrip = (trip: TripDTO) => {
    this.setState({ selectedTrip: trip, showDuplicateTripForm: true });
  };

  private getTrips = (date: Date): TripDTO[] => {
    const { trips } = this.state;
    const { user } = this.props;
    const tripsOfDate: TripDTO[] = trips
      .filter((trip: TripDTO) => {
        return new Date(trip.date).setHours(0, 0, 0, 0) === date.setHours(0, 0, 0, 0);
      })
      .filter((trip: TripDTO) => {
        if (user !== undefined && user.committeeUser !== undefined) {
          const occupation = user.committeeUser.occupation;
          if (occupation === 0) {
            // Se è dipendente, vede tutti i servizi
            return true;
          }
          // Volontario o altro, vede i servizi non destinati ai dipendenti
          return trip.type > 0;
        }
        return false;
      })
      .sort((trip1: TripDTO, trip2: TripDTO) => {
        const hour1Replaced = trip1.hour.split(':').join('');
        const hour2Replaced = trip2.hour.split(':').join('');
        return Number(hour1Replaced) - Number(hour2Replaced);
      });
    return tripsOfDate;
  };

  private handleRoleVisibleChange = (uniqueIndex: number, roleVisible: boolean) => {
    const newArray: Array<boolean> = [...this.state.rolesVisible];
    newArray[uniqueIndex] = roleVisible;
    this.setState({ rolesVisible: newArray });
  };

  private _onDeleteTripUser = (tripUserId: number) => {
    this.props.connector
      .deleteTripUsers(tripUserId)
      .then((res) => {
        this.loadData();
      })
      .catch((err) => {
        console.error(`_onDeleteTripUser: ${err}`);
        message.error(`Si è verificato un errore nell'eliminazione`);
      });
  };

  get isUserAdmin(): boolean {
    const { user } = this.props;
    if (user !== undefined && user.committeeUser !== undefined) {
      return user?.committeeUser.role < 2;
    }
    return false;
  }

  private renderTooltipUserAvatar = (trip_user: TripUsersDTO) => {
    const { user } = this.props;
    const role: string = ServiceRoleType[trip_user.role];
    return (
      <p>
        {role}
        {this.isUserAdmin || (user?.id || 0) === trip_user.userId ? (
          <Tooltip title="Elimina">
            <Button onClick={() => this._onDeleteTripUser(trip_user.id)} style={{ marginLeft: '5px' }} icon="delete" type="danger" />
          </Tooltip>
        ) : undefined}
      </p>
    );
  };

  private getAvatarBackgroundColor = (role: number): string => {
    if (role === 0) {
      return '#d9d9d9';
    } else if (role === 2) {
      return '#1890ff';
    }
    return '#87d068';
  };

  private compareTripUsers(a: TripUsersDTO, b: TripUsersDTO) {
    if (a.role === 0) {
      // Prima visualizzo gli autisti
      return -1;
    }
    if (a.role === 2) {
      // Poi do priorità al medico/sanitario
      if (b.role === 0) {
        return 1;
      }
      if (b.role === 1) {
        return -1;
      }
    }
    return 1; // Infine i soccorritori
  }

  private renderTimeline = () => {
    const { selectedDate, rolesVisible } = this.state;
    const todayTrips: TripDTO[] = this.getTrips(selectedDate);
    if (todayTrips.length === 0) {
      return <Empty />;
    }
    const tripWithMoreUsers: TripDTO = todayTrips.reduce((p: TripDTO, v: TripDTO) => {
      return p.extraUsers > v.extraUsers ? p : v;
    });
    const maximumUsers: number = tripWithMoreUsers.extraUsers + 1;
    const sharedSpan: number = Math.floor(24 / maximumUsers);
    return (
      <Timeline>
        {todayTrips.map((trip: TripDTO, index: number) => {
          const { id, vehicle, extraUsers } = trip;
          const trip_users: TripUsersDTO[] = (trip.trip_users || []).sort(this.compareTripUsers);
          const totalUsers = extraUsers + 1;
          let users: any[] = [];
          for (let index = 0; index < totalUsers; index++) {
            const trip_user: TripUsersDTO = trip_users[index];
            const uniqueIndex: number = Number(`${id}${index}`);
            const popoverVisible: boolean = rolesVisible[uniqueIndex];
            if (trip_user) {
              const user: UserDTO | undefined = trip_user.user;
              users.push(
                <Col span={sharedSpan} key={index} style={{ textAlign: 'center' }}>
                  <Tooltip title={this.renderTooltipUserAvatar(trip_user)}>
                    <Badge style={{ zIndex: 1 }} count={<b>{ServiceRoleType[trip_user.role].substring(0, 1)}</b>}>
                      <Avatar style={{ backgroundColor: this.getAvatarBackgroundColor(trip_user.role) }} shape="square" icon="user" />
                    </Badge>
                    <p>
                      <span style={{ fontSize: '10px' }}>
                        {user?.name} {user?.surname}
                      </span>
                    </p>
                  </Tooltip>
                </Col>
              );
              continue;
            }
            users.push(
              <Col span={sharedSpan} key={index} style={{ textAlign: 'center' }}>
                <Popover
                  content={this.renderRoleChoose(trip, trip_users)}
                  trigger="click"
                  visible={popoverVisible}
                  onVisibleChange={(visible: boolean) => this.handleRoleVisibleChange(uniqueIndex, visible)}
                >
                  <Button type="dashed" icon="user" />
                </Popover>
              </Col>
            );
          }
          return (
            <Timeline.Item key={index}>
              <Row>
                <Col span={8}>
                  <Row>
                    <b>Ora:</b> {trip.hour.substr(0, 5)}
                    <Tag style={{ marginLeft: '5px' }} color="green">
                      {ServiceTargetLocationType[trip.flgDestination]}
                    </Tag>
                  </Row>
                  <Row>
                    <b>Partenza: </b>
                    {trip.siteDeparture}
                  </Row>
                  <Row>
                    <b>Arrivo: </b> {trip.siteArrival}
                  </Row>
                  <Row>
                    <b>Veicolo: </b> {!!vehicle ? `${VehicleType[vehicle.type]} - ${vehicle.sign} - ${vehicle.description}` : '-'}
                  </Row>
                </Col>
                <Col span={4}>
                  <Row>
                    <Tag>{FlgArType[trip.flgAr]}</Tag>
                  </Row>
                  <Row>
                    <Tag color="cyan">{ServiceTargetRoleType[trip.type]}</Tag>
                  </Row>
                  <Row>{trip.flgNurse === 1 ? <Tag color="blue">Medico/sanitario</Tag> : undefined}</Row>
                </Col>
                <Col span={5}>
                  <Row>
                    <b>Paziente: </b>
                    {trip.patientData || '-'}
                  </Row>
                  <Row>
                    <b>Servizio: </b>
                    {trip.serviceType || '-'}
                  </Row>
                  <Row>{trip.notes}</Row>
                </Col>
                <Col span={this.isUserAdmin ? 5 : 7}>
                  <Row>{users}</Row>
                </Col>
                {this.isUserAdmin ? (
                  <Col
                    span={2}
                    style={{
                      textAlign: 'center',
                      padding: '10px'
                    }}
                  >
                    <Tooltip title="Modifica">
                      <Button size="small" icon="edit" onClick={() => this._onEditTrip(trip)} />
                    </Tooltip>
                    <Tooltip title="Duplica">
                      <Button icon="copy" size="small" style={{ marginLeft: '1px' }} onClick={() => this._onDuplicateTrip(trip)} />
                    </Tooltip>
                    <Popconfirm
                      placement="topLeft"
                      title={"Confermare l'eliminazione del servizio?"}
                      onConfirm={() => this._onDeleteTrip(trip.id)}
                      okText="Si"
                      cancelText="No"
                    >
                      <Button size="small" style={{ marginLeft: '1px' }} icon="delete" type="danger" />
                    </Popconfirm>
                  </Col>
                ) : undefined}
              </Row>
            </Timeline.Item>
          );
        })}
      </Timeline>
    );
  };

  private _onConfirmRole = (role: number, trip: TripDTO, userId: number) => {
    const tripToPublish: TripUsersDTO = {
      id: 0,
      tripId: trip.id,
      userId,
      role
    };
    const { rolesVisible } = this.state;
    const newRolesVisible = rolesVisible.map((element: boolean) => false);
    this.props.connector
      .publishTripUser(tripToPublish)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ rolesVisible: newRolesVisible }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private renderRoleChoose = (trip: TripDTO, tripUsers: TripUsersDTO[]) => {
    const { users } = this.props;
    return (
      <RoleChoose
        users={users.filter((user: UserDTO) => !!!user.committeeUser.disabled)}
        tripUsers={tripUsers}
        isUserAdmin={this.isUserAdmin}
        loggedUser={this.props.user as UserDTO}
        onConfirmRole={(role: number, userId: number) => this._onConfirmRole(role, trip, userId)}
      />
    );
  };

  private _onCreate = () => {
    this.setState({ selectedTrip: this.getEmptyTrip(), showForm: true });
  };

  private _onCloseTripForm = () => {
    this.setState({ showForm: false, selectedTrip: undefined });
  };

  private _onCloseDuplicateTripForm = () => {
    this.setState({ showDuplicateTripForm: false, selectedTrip: undefined });
  };

  private _onConfirmDuplicateTrip = async (trip: TripDTO, newDates: IDuplicateTripItem[]) => {
    let withError: boolean = false;
    for (let index = 0; index < newDates.length; index++) {
      const newDate: IDuplicateTripItem = newDates[index];
      const date: string = newDate.date.format('YYYY-MM-DD');
      const hour: string = newDate.hour.format('HH:mm');
      const newTrip: TripDTO = {
        ...trip,
        id: 0,
        date,
        hour
      };

      await this.props.connector
        .publishTrip(newTrip)
        .then((res) => {
          console.log('_onConfirmDuplicateTrip done');
        })
        // eslint-disable-next-line
        .catch((err) => {
          withError = true;
          console.error(`_onConfirmDuplicateTrip error for trip with date ${date} and hour ${hour}. Error: ${err}`);
        });
    }
    this.setState({ showDuplicateTripForm: false, selectedTrip: undefined }, () => {
      if (withError) {
        message.error(`Si sono verificati degli errori durante l'operazione`);
      } else {
        message.success('Operazione completata con successo');
      }
      this.loadData();
    });
  };

  private _onSave = (trip: TripDTO) => {
    this.props.connector
      .publishTrip(trip)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ showForm: false, selectedTrip: undefined }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onMoveViewRight = () => {
    const { viewMode, firstDayOfView } = this.state;
    var newFirstDayOfView = add(firstDayOfView, {
      days: viewMode === ViewModeType.week ? 7 : 28
    });
    this.setState({
      firstDayOfView: newFirstDayOfView,
      selectedDate: newFirstDayOfView
    });
  };

  private _onMoveViewLeft = () => {
    const { viewMode, firstDayOfView } = this.state;
    var newFirstDayOfView = add(firstDayOfView, {
      days: viewMode === ViewModeType.week ? -7 : -28
    });
    this.setState({
      firstDayOfView: newFirstDayOfView,
      selectedDate: newFirstDayOfView
    });
  };

  public render() {
    const { selectedTrip, showForm, showDuplicateTripForm, vehicles } = this.state;
    const { user } = this.props;
    return (
      <div>
        <div style={{ textAlign: 'end', margin: 10 }}>
          Visualizzazione:
          <Radio.Group defaultValue={this.state.viewMode} size="default" onChange={this._onChangeView} style={{ marginLeft: 5 }}>
            <Radio.Button value={ViewModeType.week}>Settimana</Radio.Button>
            <Radio.Button value={ViewModeType.month}>Mese</Radio.Button>
          </Radio.Group>
        </div>
        <div>
          <div>{this.renderDays()}</div>
          <Card style={{ width: '100%', height: '100%', borderRadius: '10px' }}>{this.renderTimeline()}</Card>
        </div>
        <TripForm
          visible={showForm}
          trip={selectedTrip}
          vehicles={vehicles.filter((vehicle: VehicleDTO) => !!!vehicle.disabled)}
          onClose={this._onCloseTripForm}
          onSave={this._onSave}
        />
        <DuplicateTripForm
          visible={showDuplicateTripForm}
          trip={selectedTrip}
          onClose={this._onCloseDuplicateTripForm}
          onConfirm={this._onConfirmDuplicateTrip}
        />
        {showForm || showDuplicateTripForm || (user && user.committeeUser.role > 1) ? undefined : <PlusFabButton onClick={this._onCreate} />}
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee,
    users: state.appData.users
  };
};
const ServicesContainer = connect(mapStateToProps)(Services);
export default ServicesContainer;
