import * as React from 'react';
import { Form, Button, Col, Row, Input, Select, DatePicker, Switch } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import UserDTO, { UserGenderType, UserRoleType, UserOccupationType } from '../../dto/UserDTO';
import moment from 'moment';

const { Option } = Select;

export interface IUserFormProps {
  loggedUserId: number;
  user: UserDTO | undefined;
  personalArea?: boolean;
  onCancel: () => void;
  onSave: (user: UserDTO) => void;
}

class User extends React.Component<IUserFormProps & FormComponentProps> {
  private getInitialValueBirthDate = () => {
    const { user } = this.props;
    return user?.birth_date ? moment(user.birth_date) : undefined;
  };

  private handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const userToSave: UserDTO = {
          ...values,
          birth_date: values.birth_date ? values.birth_date.format('YYYY-MM-DD') : undefined,
          committeeUser: {
            id: values.committeeUserId,
            userId: values.id,
            committeeId: values.committeeId,
            role: values.role,
            occupation: values.occupation,
            disabled: values.disabled
          }
        };
        this.props.onSave(userToSave);
      }
    });
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    const { user, onCancel, personalArea } = this.props;
    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="Username">
              {getFieldDecorator('username', {
                initialValue: this.props.user?.username,
                rules: [{ required: true, message: 'Specificare il nome utente' }]
              })(<Input disabled={user && user.id > 0} placeholder="Specificare il nome utente" />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Password">
              {getFieldDecorator('password', {
                initialValue: this.props.user?.password,
                rules: [{ required: true, message: 'Specificare la password' }]
              })(
                personalArea ? (
                  <Input.Password disabled={true} placeholder="Specificare la password" />
                ) : (
                  <Input disabled={user && user.id > 0} type="password" placeholder="Specificare la password" />
                )
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="Nome">
              {getFieldDecorator('name', {
                initialValue: this.props.user?.name,
                rules: [{ required: true, message: 'Specificare il nome' }]
              })(<Input placeholder="Specificare il nome" />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Cognome">
              {getFieldDecorator('surname', {
                initialValue: this.props.user?.surname,
                rules: [{ required: true, message: 'Specificare il cognome' }]
              })(<Input placeholder="Specificare il cognome" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <Form.Item label="Data di nascita">
              {getFieldDecorator('birth_date', {
                initialValue: this.getInitialValueBirthDate(),
                rules: []
              })(<DatePicker format="DD-MM-YYYY" />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Sesso">
              {getFieldDecorator('gender', {
                initialValue: this.props.user?.gender,
                rules: [
                  {
                    required: true,
                    message: 'Specificare il sesso'
                  }
                ]
              })(
                <Select placeholder="Sesso">
                  <Option value={0}>{UserGenderType[0]}</Option>
                  <Option value={1}>{UserGenderType[1]}</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <Form.Item label="Email">
              {getFieldDecorator('email', {
                initialValue: this.props.user?.email,
                rules: []
              })(<Input placeholder="Specificare l'email" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16} style={{ visibility: personalArea ? 'collapse' : 'visible' }}>
          <Col span={12}>
            <Form.Item label="Ruolo">
              {getFieldDecorator('role', {
                initialValue: this.props.user?.committeeUser.role,
                rules: [
                  {
                    required: true,
                    message: 'Specificare il ruolo'
                  }
                ]
              })(
                <Select placeholder="Role">
                  <Option value={1}>{UserRoleType[1]}</Option>
                  <Option value={2}>{UserRoleType[2]}</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Occupazione">
              {getFieldDecorator('occupation', {
                initialValue: this.props.user?.committeeUser.occupation,
                rules: [
                  {
                    required: true,
                    message: `Specificare l'occupazione`
                  }
                ]
              })(
                <Select placeholder="Occupazione">
                  <Option value={0}>{UserOccupationType[0]}</Option>
                  <Option value={1}>{UserOccupationType[1]}</Option>
                  <Option value={2}>{UserOccupationType[2]}</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row style={{ visibility: personalArea ? 'collapse' : 'visible' }}>
          <Col span={12}>
            <Form.Item label="Con patente">
              {getFieldDecorator('withDrivingLicense', {
                initialValue: this.props.user?.withDrivingLicense,
                valuePropName: 'checked'
              })(<Switch />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Disabilita">
              {getFieldDecorator('disabled', {
                initialValue: this.props.user?.committeeUser.disabled,
                valuePropName: 'checked'
              })(<Switch />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={16} style={{ visibility: 'collapse' }}>
          <Col span={6}>
            <Form.Item label="Id">
              {getFieldDecorator('id', {
                initialValue: this.props.user?.id
              })(<Input placeholder="Specificare id utente" />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Password modificata">
              {getFieldDecorator('flgPasswordChanged', {
                initialValue: this.props.user?.flgPasswordChanged
              })(<Input placeholder="Password modificata" />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CommitteeUserId">
              {getFieldDecorator('committeeUserId', {
                initialValue: this.props.user?.committeeUser.id
              })(<Input placeholder="CommitteeUserId" />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CommitteeId">
              {getFieldDecorator('committeeId', {
                initialValue: this.props.user?.committeeUser.committeeId
              })(<Input placeholder="CommitteeId" />)}
            </Form.Item>
          </Col>
        </Row>
        <div
          style={{
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: '100%',
            borderTop: '1px solid #e9e9e9',
            padding: '10px 16px',
            background: '#fff',
            textAlign: 'right'
          }}
        >
          <Button onClick={onCancel} style={{ marginRight: 8 }}>
            Annulla
          </Button>
          <Button htmlType="submit" type="primary">
            Salva
          </Button>
        </div>
      </Form>
    );
  }
}

const UserContainer = Form.create({ name: 'user_form ' })(User);
class UserForm extends React.Component<IUserFormProps> {
  public render() {
    return <UserContainer {...this.props} />;
  }
}

export default UserForm;
