import React from 'react';
import { Layout } from 'antd';
import { RouterProps } from 'react-router';
import RedCrossMenu from './base/RedCrossMenu';

const { Footer } = Layout;

export interface RedCrossFooterProps extends RouterProps {
  isUserAdmin: boolean;
}

class RedCrossFooter extends React.Component<RedCrossFooterProps> {
  public render() {
    const { history, isUserAdmin } = this.props;
    return (
      <Footer style={{ width: '100%', position: 'fixed', left: 0, bottom: 0, padding: '0px' }}>
        <RedCrossMenu isUserAdmin={isUserAdmin} history={history} menuMode={'horizontal'} />
      </Footer>
    );
  }
}
export default RedCrossFooter;
