import CommitteeDTO from './CommitteeDTO';
import VehicleDTO from './VehicleDTO';
import TripUsersDTO from './TripUsersDTO';

export default interface TripDTO {
  id: number;
  date: string;
  hour: string; // Esiste il time?
  flgDestination: number;
  flgAr: number;
  siteDeparture: string;
  siteArrival: string;
  serviceType: string;
  patientData?: string;
  type: number;
  extraUsers: number;
  flgNurse: number;
  flgToNotify: number;
  notes?: string;
  committeeId: number;
  vehicleId?: number;
  committee: CommitteeDTO;
  vehicle?: VehicleDTO;
  trip_users?: TripUsersDTO[];
}

export const ServiceTargetRoleType: { [code: number]: string } = {
  0: 'Dipendenti',
  1: 'Volontari',
  2: 'Chiunque'
};

export const ServiceTargetLocationType: { [code: number]: string } = {
  0: 'In Sede',
  1: 'Destinazione',
  2: 'Prelevamento'
};

export const FlgArType: { [code: number]: string } = {
  0: 'Solo Andata',
  1: 'Andata/Ritorno'
};
