import React from 'react';
import { connect } from 'react-redux';
import { Layout } from 'antd';
import { Route } from 'react-router-dom';
import { Links } from '../utils/Links';
import UsersContainer from './Users';
import VehiclesContainer from './Vehicles';
import ServicesContainer from './Services';
import { WithUser, WithCommittee } from '../reducers/reducers';
import { RouterProps } from 'react-router';
import { MAXIMUM_WIDTH_FOR_SMARTPHONE, MAXIMUM_WIDTH_FOR_TABLET } from '../utils/Constants';
import { IResetAuthDataProps, resetAuthData } from '../actions/action';
import PersonalAreaContainer from './PersonalArea';
import RedCrossSider from './layouts/RedCrossSider';
import RedCrossHeader from './layouts/RedCrossHeader';
import RedCrossFooter from './layouts/RedCrossFooter';

const { Content } = Layout;

interface DashboardProps extends WithUser, WithCommittee, IResetAuthDataProps, RouterProps {}

interface DashboardState {
  windowWidth: number;
  windowHeight: number;
}

class Dashboard extends React.Component<DashboardProps, DashboardState> {
  constructor(props: DashboardProps) {
    super(props);

    const { user, committee } = this.props;
    if (!user && !committee) {
      this._onLogout();
    }
    this.state = {
      windowWidth: 0,
      windowHeight: 0
    };
  }

  componentDidMount() {
    this.updateDimensions();
    window.addEventListener('resize', this.updateDimensions.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions.bind(this));
  }

  updateDimensions() {
    const windowWidth = typeof window !== 'undefined' ? window.innerWidth : 0;
    const windowHeight = typeof window !== 'undefined' ? window.innerHeight : 0;

    this.setState({ windowWidth, windowHeight });
  }

  _onClickLogout = () => {
    this.props.resetAuthData();
    this._onLogout();
  };

  _onLogout = () => {
    this.props.history.replace(Links.LOGOUT);
  };

  get isUserAdmin(): boolean {
    const { user } = this.props;
    if (user !== undefined && user.committeeUser !== undefined) {
      return user?.committeeUser.role < 2;
    }
    return false;
  }

  get isForSmartphone(): boolean {
    const { windowWidth } = this.state;
    return windowWidth <= MAXIMUM_WIDTH_FOR_SMARTPHONE;
  }

  get isForTablet(): boolean {
    const { windowWidth } = this.state;
    return windowWidth > MAXIMUM_WIDTH_FOR_SMARTPHONE && windowWidth < MAXIMUM_WIDTH_FOR_TABLET;
  }

  get isForDesktop(): boolean {
    const { windowWidth } = this.state;
    return windowWidth >= MAXIMUM_WIDTH_FOR_TABLET;
  }

  public render() {
    const { user, committee } = this.props;
    return (
      <Layout style={{ minHeight: '100vh' }}>
        {this.isForDesktop ? <RedCrossSider isUserAdmin={this.isUserAdmin} history={this.props.history} /> : undefined}
        <Layout>
          <RedCrossHeader committee={committee} user={user} onClickLogout={this._onClickLogout} history={this.props.history} />
          <Content style={{ margin: '0 16px' }}>
            <Route path={Links.SERVICES} component={ServicesContainer} />
            <Route path={Links.USERS} component={UsersContainer} />
            <Route path={Links.VEHICLES} component={VehiclesContainer} />
            <Route path={Links.PERSONAL_AREA} component={PersonalAreaContainer} />
          </Content>
          {!this.isForDesktop ? <RedCrossFooter isUserAdmin={this.isUserAdmin} history={this.props.history} /> : undefined}
        </Layout>
      </Layout>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    committee: state.appData.committee
  };
};
const DashboardContainer = connect(mapStateToProps, { resetAuthData })(Dashboard);
export default DashboardContainer;
