import axios, { AxiosInstance } from 'axios';
import VehicleDTO from '../dto/VehicleDTO';
import UserDTO from '../dto/UserDTO';
import CommitteeUserDTO from '../dto/CommitteeUserDTO';
import TripDTO from '../dto/TripDTO';
import TripUsersDTO from '../dto/TripUsersDTO';
import { getUserInfo } from './Utils';
import LoggedUserInfoDTO from '../dto/LoggedUserInfoDTO';

export class Connector {
  private axiosInstance: AxiosInstance;

  constructor() {
    const headers = {
      'Content-Type': 'application/json'
    };

    let baseURL: string | undefined = undefined;
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      baseURL = 'http://localhost:3001';
    }
    this.axiosInstance = axios.create({
      baseURL,
      headers
    });
  }

  public async getCommittees(): Promise<any> {
    const operation = 'api/committees';
    return await this.call(operation);
  }

  public async getVehicles(committeeId: number): Promise<any> {
    const operation = `api/vehicles/findByCommittee/${committeeId}`;
    return await this.call(operation);
  }

  public async publishVehicle(vehicle: VehicleDTO): Promise<any> {
    const { id } = vehicle;
    const operation = `api/vehicles/${id > 0 ? id : ''}`;
    const body = JSON.stringify(vehicle);
    return await this.call(operation, body, id > 0 ? 'PUT' : 'POST');
  }

  public async deleteVehicle(vehicle: VehicleDTO): Promise<any> {
    const { id } = vehicle;
    const operation = `api/vehicles/${id}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async deleteTripUsersByTrip(tripId: number): Promise<any> {
    const operation = `api/trips_users/deleteByTrip/${tripId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async deleteTripUsers(tripUserId: number): Promise<any> {
    const operation = `api/trips_users/${tripUserId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async deleteCommitteesUsers(userId: number): Promise<any> {
    const operation = `api/committees_users/deleteByUserId/${userId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async getUserTrips(userId: number): Promise<any> {
    const operation = `api/trips_users/findByUserId/${userId}`;
    return await this.call(operation);
  }

  public async deleteTrip(tripId: number): Promise<any> {
    const operation = `api/trips/${tripId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async getTrips(committeeId: number): Promise<any> {
    const operation = `api/trips/findByCommittee/${committeeId}`;
    return await this.call(operation);
  }

  public async publishTrip(trip: TripDTO): Promise<any> {
    const { id } = trip;
    const operation = `api/trips/${id > 0 ? id : ''}`;
    const body = JSON.stringify(trip);
    return await this.call(operation, body, id > 0 ? 'PUT' : 'POST');
  }

  public async publishTripUser(tripUser: TripUsersDTO): Promise<any> {
    const { id } = tripUser;
    const operation = `api/trips_users/${id > 0 ? id : ''}`;
    const body = JSON.stringify(tripUser);
    return await this.call(operation, body, id > 0 ? 'PUT' : 'POST');
  }

  public async getUser(userId: number): Promise<any> {
    const operation = `api/users/${userId}`;
    return await this.call(operation);
  }

  public async getUsers(committeeId: number): Promise<any> {
    const operation = `api/users/findByCommittee/${committeeId}`;
    return await this.call(operation);
  }

  public async deleteUser(userId: number): Promise<any> {
    const operation = `api/users/${userId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async deleteCommitteeUser(committeeUserId: number): Promise<any> {
    const operation = `api/committees_users/${committeeUserId}`;
    return await this.call(operation, undefined, 'DELETE');
  }

  public async publishUser(user: UserDTO): Promise<any> {
    const { id } = user;
    const operation = `api/users/${id > 0 ? id : ''}`;
    const body = JSON.stringify(user);
    return this.call(operation, body, id > 0 ? 'PUT' : 'POST');
  }

  public async publishUserCommittee(user: UserDTO, committeeUserInfo: CommitteeUserDTO): Promise<any> {
    const userResponse = await this.publishUser(user);
    if (userResponse.status !== 200) {
      console.log(userResponse);
      throw new Error(`Si è verificato un errore nell'inserimento`);
    }

    const { id } = user;
    const publishedUser: UserDTO = userResponse.data;
    const committeeUser: CommitteeUserDTO = {
      id: committeeUserInfo.id,
      userId: id > 0 ? id : publishedUser.id,
      committeeId: committeeUserInfo.committeeId,
      role: committeeUserInfo.role,
      occupation: committeeUserInfo.occupation,
      disabled: committeeUserInfo.disabled
    };
    const bodyCommUser = JSON.stringify(committeeUser);
    const operationCommUser = `api/committees_users/${committeeUserInfo.id > 0 ? committeeUserInfo.id : ''}`;
    return await this.call(operationCommUser, bodyCommUser, committeeUserInfo.id > 0 ? 'PUT' : 'POST');
  }

  public async signin(username: string, password: string, committeeId: number): Promise<any> {
    let baseURL: string | undefined = undefined;
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      baseURL = 'http://localhost:3001';
    }
    const tempInstance = axios.create({
      baseURL
    });
    return tempInstance({
      url: `api/auth/signin`,
      method: 'POST',
      data: {
        username,
        password,
        committeeId
      }
    });
  }

  public async findUserByUsername(username: string, password: string): Promise<any> {
    const operation = `api/users/findbyusername/${username}`;
    const hash: string = btoa(`${username}:${password}`);
    const headers = {
      Authorization: `Basic ${hash}`
    };
    let baseURL: string | undefined = undefined;
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      baseURL = 'http://localhost:3001';
    }
    const tempInstance = axios.create({
      baseURL,
      headers
    });
    return tempInstance({
      url: operation,
      method: 'GET'
    });
  }

  private authHeader = () => {
    const userInfo: LoggedUserInfoDTO | undefined = getUserInfo();
    if (userInfo && userInfo.accessToken) {
      // for Node.js Express back-end
      return { 'x-access-token': userInfo.accessToken };
    } else {
      return {};
    }
  };

  /**
   * @param url
   * @param body stringa (in json) oppure UrlSearchParams per gli environment
   * @param method
   */
  private async call(url: string, body?: string, method: 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE' = 'GET'): Promise<any> {
    return this.axiosInstance({
      url,
      method,
      headers: this.authHeader(),
      data: body
    });
  }
}
